import React, { useState } from 'react';
import { Typography, Select, MenuItem, TextField } from '@mui/material';
import { observer } from 'mobx-react';

import Button from '../../../Button';
import { POSTCODE_API_KEY, POSTCODE_URL } from '../../../../config';
import API from '../../../../utils/api';
import WizardStore from '../../../../store/WizardStore';
import PostCodeStore from '../../../../store/PostCodeStore';
import AppStore from '../../../../store/AppStore';
import { inputPostcode } from '../../../../constants/inputPostcode';
import styles from './Postcodes.module.scss';
import arrowWhite from '../../../../images/ArrowWhite.svg';
import { inputSelect } from '../../../../constants/inputSelect';
import { BASE_URL, TOKEN } from '../../../../config';
import getPostCodeInfo from '../../../../utils/getPostCodeInfo';

const Postcodes = () => {
  const [selectedAddress, setSelectedAddress] = useState('');
  const [addressInfo, setAddressInfo] = useState(false);
  const [postCodeValue, setPostCodeValue] = useState('');
  const [postCodeError, setPostCodeError] = useState('');

  const handleChangePostCode = value => {
    setSelectedAddress('');
    setAddressInfo(false);
    setPostCodeValue(value.target.value);
    PostCodeStore.updateCodes([]);
  };

  const handleChangeAddress = value => {
    const addressId = value.target.value;
    const currentAddress = PostCodeStore.postCodes.find(
      code => code.id === addressId,
    );
    setSelectedAddress(currentAddress.line_1);
    setAddressInfo(currentAddress);

    AppStore.updateAddress1(currentAddress.line_1);
    AppStore.updateAddress2(currentAddress.line_2);
    AppStore.updateCity(currentAddress.county);
    AppStore.updateCounty(currentAddress.county);
    AppStore.updatePostcode(currentAddress.postcode);
  };

  const findAddresses = () => {
    const axiosConfig = {
      url: `${POSTCODE_URL}/${postCodeValue}`,
      method: 'GET',
      params: { api_key: POSTCODE_API_KEY },
    };

    API(axiosConfig)
      .then(response => {
        setAddressInfo(false);
        AppStore.updateAddress1('');
        AppStore.updateAddress2('');
        AppStore.updateCity('');
        AppStore.updateCounty('');
        AppStore.updatePostcode('');
        if (response?.err) {
          setPostCodeError('Please, try again.');
          PostCodeStore.updateCodes([]);
          return;
        }
        setPostCodeError('');
        PostCodeStore.updateCodes(response.response.data.result);
      })
      .catch(error => {});
  };

  const sendPostCodes = () => {
    const id = AppStore.client_ID;

    const config = {
      url: `${BASE_URL}/${id}`,
      method: 'PATCH',
      headers: {
        Authorization: `Bearer ${TOKEN}`,
      },
      data: getPostCodeInfo(),
    };
    API(config)
      .then(response => {
        AppStore.updateClient_ID(response.response.data.id);
        AppStore.updateClient_ref(response.response.data.ref);
      })
      .catch(error => {});
  };

  const textStyles = {
    color: '#1A374D',
    fontWeight: '700',
    fontSize: '19px',
    lineHeight: '26px',
    textAlign: 'left',
    marginBottom: '20px',
  };

  const isDisabled =
    !AppStore.address1 || PostCodeStore?.postCodes?.length === 0;

  return (
    <>
      <Typography variant="h3" sx={textStyles}>
        Thank you, {AppStore.first_name}. We just need to know where to issue
        your refund.
      </Typography>
      <Typography variant="h3" sx={textStyles}>
        What is your postcode?
      </Typography>
      <div>
        <>
          <div className={styles.wrapper}>
            <TextField
              onChange={handleChangePostCode}
              value={postCodeValue}
              placeholder="Enter your postcode"
              sx={inputPostcode}
            />
            {PostCodeStore.postCodes.length > 0 && (
              <div className={styles.selectAddress}>
                <Select
                  sx={inputSelect}
                  renderValue={selected => {
                    if (selected.length === 0) {
                      return <span>Select your address</span>;
                    }
                    return selected;
                  }}
                  value={selectedAddress}
                  displayEmpty
                  onChange={handleChangeAddress}
                >
                  <MenuItem
                    disabled
                    value=""
                    sx={{
                      fontFamily: 'Inter, sans-serif',
                      color: '#A1BFC6',
                      fontSize: '19px',
                      fontWeight: 400,
                      fontStyle: 'normal',
                    }}
                  >
                    <span>Select your address</span>
                  </MenuItem>
                  {PostCodeStore.postCodes.length > 0 &&
                    PostCodeStore.postCodes.map(item => (
                      <MenuItem value={item.id} key={item.id}>
                        {item.line_3} {item.line_1}
                      </MenuItem>
                    ))}
                </Select>
              </div>
            )}
          </div>
          <div>
            <button
              onClick={findAddresses}
              disabled={!postCodeValue}
              className={styles.btnFindAddress}
            >
              Find my address
              <img
                style={{
                  width: '15px',
                  height: '13px',
                  marginLeft: '9px',
                  paddingRight: '15px',
                }}
                src={arrowWhite}
                alt="arrow"
              />
            </button>
          </div>
          {addressInfo && (
            <div className={styles.addressTextWrapper}>
              {addressInfo?.line_1 && (
                <div className={styles.addressText}>{addressInfo?.line_1}</div>
              )}
              {addressInfo?.line_2 && (
                <div className={styles.addressText}>{addressInfo?.line_2}</div>
              )}
              {addressInfo?.line_3 && (
                <div className={styles.addressText}>{addressInfo?.line_3}</div>
              )}
              {addressInfo?.county && (
                <div className={styles.addressText}>{addressInfo?.county}</div>
              )}
              {addressInfo?.postcode && (
                <div className={styles.addressText}>
                  {addressInfo?.postcode}
                </div>
              )}
            </div>
          )}
        </>
        {postCodeError}
      </div>
      <Button
        onClick={() => {
          WizardStore.nextStep();
          sendPostCodes();
        }}
        disabled={isDisabled}
        display="form"
      >
        Next
      </Button>
    </>
  );
};

export default observer(Postcodes);
