import AppStore from '../store/AppStore';

const getClientInfo = () => {
  const selectedYears = AppStore.chipsYears.filter(
    item => item.selected === true,
  ).length;

  const multiple_claims = AppStore.offerAmount / selectedYears;
  const roundedMultiple_claims = multiple_claims.toFixed(2);
  const tax_deducted = roundedMultiple_claims * 0.11;
  const roundedTax_deducted = tax_deducted.toFixed(2);
  const gross_interest = roundedTax_deducted * 5;
  const roundedGross_interest = gross_interest.toFixed(2);
  const net_interest = roundedGross_interest - roundedTax_deducted;

  const selectedBanks = AppStore.chipsBanks.filter(
    item => item.selected === true,
  );

  const banksToString = () => {
    const bankArr = [];
    selectedBanks.map(item => bankArr.push(item.value));
    return bankArr.join('; ');
  };

  const generateClaimAttributes = () => {
    const selectedYears = AppStore.chipsYears.filter(
      item => item.selected === true,
    );

    const result = selectedYears.map(item => {
      return {
        claim_type: 'ppi',
        status_id: 141,
        outcome_id: 13,
        pba_ppis_attributes: [
          {
            offer_date: `06/04/${item.value}`,
            lender:
              selectedBanks.length === 1 ? AppStore.chipsBanks[0].value : '',
            offer_amount: roundedMultiple_claims,
            gross_interest: roundedGross_interest,
            tax_deducted: roundedTax_deducted,
            net_interest: net_interest,
            tax_year: `${item.taxPeriod}`,
          },
        ],
      };
    });
    return result;
  };

  return {
    first_name: AppStore.first_name,
    last_name: AppStore.last_name,
    mobile_phone: AppStore.mobile_phone,
    email: AppStore.email,
    dob: `${AppStore.day}/${AppStore.month}/${AppStore.year}`,
    source_ref: 'MK01',
    extra_info: selectedBanks.length > 1 ? banksToString() : 'Test extra info',
    employment: AppStore.employment_status,
    income_bracket2022: AppStore.income,
    claims_attributes: generateClaimAttributes(),
    CRM_Status_ID: 139,
    CRM_Outcome_ID: 94,
    Status: 'Created From Marketing',
  };
};

export default getClientInfo;
