import AppStore from '../store/AppStore';

const getQualifiers = () => {
  const formatQuestion = value => {
    if (value === 'Yes') {
      return true;
    }
    return false;
  };

  return {
    client_id: AppStore.client_ID,
    self_assessment: formatQuestion(AppStore.self_assessment),
    required_uniform: formatQuestion(AppStore.required_uniform),
    married: formatQuestion(AppStore.married),
    pba_ppi_claim: true,
    vehicle_required: AppStore.vehicle_required === 'Yes' ? true : false,
  };
};

export default getQualifiers;
