import React, { useState } from 'react';
import { Typography, Checkbox } from '@mui/material';
import { observer } from 'mobx-react';

import { SIGNATURE_URL, TOKEN } from '../../../../config';
import API from '../../../../utils/api';
import FormSignature from '../Signature/FormSignature';
import WizardStore from '../../../../store/WizardStore';
import Button from '../../../Button';
import AppStore from '../../../../store/AppStore';
import getSignatureDetails from '../../../../utils/getSignatureDetails';

const Signature = () => {
  const [signaturePad, setSignaturePad] = useState(false);

  const handleCheckboxChange = value => {
    AppStore.updateSignatureChecked(value.target.checked);
  };

  const handleClear = () => {
    setSignaturePad(false);
    AppStore.signature.clear();
    AppStore.signature.isEmpty();
  };

  const sendSignature = () => {
    const config = {
      url: `${SIGNATURE_URL}`,
      method: 'POST',
      headers: {
        Authorization: `Bearer ${TOKEN}`,
      },
      data: getSignatureDetails(),
    };
    API(config)
      .then(response => {})
      .catch(error => {});
  };

  const handleChangeSignature = value => {
    setSignaturePad(value);
  };

  const isDisabled = !AppStore.signatureChecked === true || !signaturePad;

  return (
    <>
      <Typography
        variant="h3"
        sx={{
          color: '#1A374D',
          fontWeight: '700',
          fontSize: '17px',
          lineHeight: '25px',
          textAlign: 'left',
          marginBottom: '30px',
          maxWidth: '291px',
        }}
      >
        Please draw your signature in the box below.
      </Typography>
      <Typography
        variant="h3"
        sx={{
          color: '#1A374D',
          fontWeight: '700',
          fontSize: '17px',
          lineHeight: '25px',
          textAlign: 'left',
          marginBottom: '30px',
        }}
      >
        Take your time to make it accurate.
      </Typography>
      <Typography
        variant="h3"
        sx={{
          color: '#1A374D',
          fontWeight: '700',
          fontSize: '17px',
          lineHeight: '25px',
          textAlign: 'left',
          marginBottom: '40px',
        }}
      >
        You can start again as many times as you like by pressing Clear.
      </Typography>
      <FormSignature
        onClick={handleClear}
        isSignature={handleChangeSignature}
      />
      <div style={{ display: 'flex', marginBottom: '50px' }}>
        <Checkbox
          sx={{
            marginRight: '18px',
            padding: '15px',
            color: '#47C9A7',
            background: '#F9F9F9 0% 0 % no - repeat padding- box',
            width: '17px',
            height: '17px',
          }}
          onChange={handleCheckboxChange}
          inputProps={{ 'aria-label': 'controlled' }}
        />
        <Typography
          sx={{
            paddingTop: '6px',
            color: '#1A374D',
            fontSize: '15px',
            fontWeight: '400',
            lineHeight: '23px',
          }}
        >
          This is a true likeness of my signature
        </Typography>
      </div>
      <Button
        onClick={() => {
          WizardStore.nextStep();
          sendSignature();
        }}
        disabled={isDisabled}
        display="form"
      >
        Next
      </Button>
    </>
  );
};

export default observer(Signature);
