import React, { useEffect, useRef } from 'react';
import Signature from 'signature_pad';
import AppStore from '../../../../store/AppStore';
import styles from './FormSignature.module.scss';

const FormSignature = ({ onClick, isSignature }) => {
  const defaultSignature =
    'data:image/png;base64,';

  const readyPad = () => {
    let wrapper = document.getElementById('signature-pad');
    let canvas = wrapper?.querySelector('canvas');
    canvas.getContext('2d').scale(1, 1);
    let signaturePad = new Signature(canvas, {
      border: '1px solid #000000',
    });
    AppStore.updateSignature(signaturePad);
  };

  const myRef = useRef(null);

  const getImg = () => {
    const img = myRef.current.toDataURL();
    if (defaultSignature === img) {
      isSignature(false);
      return;
    }
    AppStore.updateSignatureImg(img);
    isSignature(true);
  };

  useEffect(() => {
    readyPad();
  }, []);

  return (
    <div className={styles.wrapper} id="signature-pad">
      <canvas
        className={styles.signature}
        width="250"
        height="250"
        ref={myRef}
        onClick={() => getImg()}
        onTouchEnd={() => getImg()}
      ></canvas>
      <div>
        <button className={styles.btnClear} onClick={onClick}>
          Clear
        </button>
      </div>
    </div>
  );
};
export default FormSignature;
